  <div>
    <h1>Комнаты конференции</h1>
    <video id="myStream" autoplay playsinline controls="false"/>
    <video id="otherStream" autoplay playsinline controls="false"/>
    <button @click="rt()">click me</button>
    <!-- <ul>
      <li v-for="roomID in rooms" :key="roomID">
        {{roomID}}
      </li>
    </ul>
    <div>
      <div v-for="client in this.clients" :key="client">
