<script>
import socket from '../socket'
import ACTIONS from '../socket/actions'

// import useWebRTC from './useWebRTC'

export default {
  name: 'App',
  data(){
    return{
      // rooms: [],
      // clients: useWebRTC(roomID)
      // clients: [],
      // peerConnections: {},
