      // qw:true
    }
  },
  // components: {useWebRTC},
  methods:{
    rt(){
      // this.qw = false
      let otherPeers = []
      let pc = new RTCPeerConnection({"iceServers": [{"url": "stun:provserver.televolution.net"}, {"url":"stun:stun.ekiga.net"}]})
      let offerSent = false
      let stream = null
      let clientSocket = null

    function setupClient(){
      socket.on('client_socket', function(data){
        if(data === '0'){
          pc.createOffer(gotOfferDescription)
          offerSent = true
        }else if(data.type === 'description'){
          if(!offerSent){
            pc.setRemoteDescription(new RTCSessionDescription(data.info))
            pc.createAnswer(gotAnswerDescription)
          }else{
          pc.setRemoteDescription(new RTCSessionDescription(data.info))
          }
        }else if(data.rtpe === 'candidate'){
          pc.addIceCandidate(new RTCIceCandidate(data.info))
        }else if(data === 'reset'){
          pc.close()
          alert('User Exit')
        }
      })
    }


      function gotOfferDescription(desc){
        pc.setLocalDescription(desc)
        socket.emit('server_socket', {type:'description', info:desc})
      }

      function gotAnswerDescription(desc){
        pc.setLocalDescription(desc)
        socket.emit('server_socket', {type:'description', info:desc})
      }

      pc.onicecandidate = function(event){
        if(event.candidate){
          socket.emit('server_socket', {type:'candidate', info:event.candidate})
        }
      }


      function handleTrackEvent(event) {
        const videoElement = document.querySelector('video#otherStream');
        videoElement.volume = 0
        videoElement.srcObject = event.streams[0];
        alert('пошел')
      }
      // pc.ontrack = function(e){
      //   const videoElement = document.querySelector('video#otherStream');
      //   videoElement.volume = 0
      //   videoElement.srcObject = e.stream;
      //   alert('пошел')
      // }

      pc.onerror = function(){
        // Error
      }

      const constraints = {
        'video': true,
        'audio': true
      }

      navigator.mediaDevices.getUserMedia(constraints)
          .then(stream => {
              const videoElement = document.querySelector('video#myStream');
              videoElement.volume = 0
              videoElement.srcObject = stream;

              pc.ontrack = handleTrackEvent
              stream.getTracks().forEach(function(track) {
                pc.addTrack(track, stream);
              });
              setupClient()
          })
          .catch(error => {
              console.error('Error accessing media devices.', error);
          });
      }
    // addNewClient(newClient, cb){
    //       if(!this.clients.includes(newClient)){
    //           this.clients = (list => [...list, newClient], cb)
    //       }
    // }
  },
